
import * as path from "path-browserify";
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    src: {
      type: String,
      required: true
    },
    sizes: {
      type: String,
      required: false,
      default: "100vw, (min-width: 576px) 50vw, (min-width: 1200px) 30vw"
    },
    alt: {
      type: String,
      required: false,
      default: ""
    },
    title: {
      type: String,
      required: false,
      default: ""
    },
    loading: {
      type: String,
      required: false,
      default: "lazy"
    }
  },

  data() {
    return {
      iu: path.parse(path.join("/img", this.src))
    };
  }
});
